@import url('https://fonts.googleapis.com/css?family=Kreon|Ubuntu');

body {
  background-color: #1B1B1E;
  color: #D8DBE2;
}

h1,h2,h3,h4,h5,h6 {
  color: #A9BCD0;
  font-family: 'Kreon', serif;
}

p,a,div,span,nav,aside,section,button {
  color: #D8DBE2;
  font-family: 'Ubuntu', sans-serif;
}

h1#main-header {
  font-size: 2.75rem;
}

div.post-list h1 {
  font-size: 2rem;
}



